// Modal

const links = [...document.querySelectorAll('.linkToPay')]
const modal = document.querySelector('.modal')
const closeModal = document.querySelector('.modal .close')
const textPrice = {
  post15: 'Здравствуйте, хочу преобрести у Вас услугу "Визуал на 15 постов" стоимостью 5 500 р',
  post30: 'Здравствуйте, хочу преобрести у Вас услугу "Визуал на 30 постов" стоимостью 9 500 р',
  content15: 'Здравствуйте, хочу преобрести у Вас услугу "Визуальный контент-план на 15 постов" стоимостью 1 300 р',
  content30: 'Здравствуйте, хочу преобрести у Вас услугу "Визуальный контент-план на 30 постов" стоимостью 2 500 р',
}

let finishText = ''

links.forEach(el => el.onclick = (e) => {
  const text = el.getAttribute('data-text')
  finishText = textPrice[text]
  e.preventDefault()
  modal.classList.add('open')
})

const closeModalFunc = () => {
  modal.classList.add('hide')
  setTimeout(()=> {
    modal.classList.remove('open', 'hide')
  }, 1000)
}

modal.onclick = (e) => {
  if (e.target === modal) closeModalFunc()
}
closeModal.onclick = () => closeModalFunc()

// socal link

const socialLinks = [...document.querySelectorAll('.modal a')]

socialLinks.forEach(el => {
  el.onclick = () => {
    const social = el.getAttribute('data-soc')
    const a = document.createElement('a')
    a.target = '_blank'
    const number = '79526630170'
    const messangers = {
      telegram: `tg://resolve?domain=OceanMuse&text=${finishText}`,
      what: `https://wa.me/+${number}?text=${finishText}`,
      inst: `https://instagram.com/ocean_muse_`
    }
    a.href = messangers[social]
    a.click()
  }
})

